import Step from '../../base/step/step.vue'
import Scroll from '../../base/scroll/scroll.vue'
export default {
  name: 'textKey',
  components: {
    Step,
    Scroll
  },
  data () {
    return {
      isOk: true,
      aaa: '54454',
      keyWords: 'aa',
      msgCont: '',
      goodsObj: {},
      shopName: '',
      goodCommon: '',
      rbObj: {}
    }
  },
  created () {
    // 获取商品详情
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      'buyerTaskRecordId': this.$route.query.buyerTaskRecordId
    }).then((data) => {
      console.log(data)
      if (data.data.code === '200') {
        this.goodsObj = data.data.data
        this.shopName = this.goodsObj.shopName
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      } else {
        this.$vux.alert.show({
          title: '获取信息失败',
          content: data.data.message
        })
      }
    }).catch((err) => {
      console.log(err)
    })
    if (this.$route.query.rb) {
      // 获取与评价的内容
      this.$axios.post('/api/orderOperate/getTaskRecordByOrderId', {
        'orderId': this.$route.query.buyerTaskRecordId
      }).then((data) => {
        if (data.data.code === '200') {
          this.rbObj = data.data.data
          this.goodCommon = this.rbObj.favorText
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        } else {
          this.$vux.alert.show({
            title: '获取信息失败',
            content: data.data.message
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  },
  methods: {
    doNext () {
      if (this.goodCommon === '') {
        this.$vux.alert.show({
          title: '提示',
          content: '请填写评论信息!'
        })
        return false
      }
      this.$axios.post('/api/orderOperate/backOrderSubmit', {
        buyerTaskRecordId: this.$route.query.buyerTaskRecordId,
        favorText: this.goodCommon
      }).then((data) => {
        console.log(data)
        if (data.data.code === '200') {
          this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate1' } })
        } else {
          this.$vux.alert.show({
            title: '提交失败',
            content: data.data.message
          })
        }
      }).catch(() => {
        this.$vux.alert.show({
          title: '错误提示',
          content: '服务器错误'
        })
      })
    }
  }
}



